img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input::-webkit-input-placeholder {
  text-transform: $uppercase;
}

input:-moz-placeholder {
  text-transform: $uppercase;
}

textarea::-webkit-input-placeholder {
  text-transform: $uppercase;
}

textarea:-moz-placeholder {
  text-transform: $uppercase;
}
